.page {
  background: linear-gradient(135deg, #dcd6f9, #c4baf8);
  min-height: 100vh;
  padding: 40px;
}

.title {
  text-align: center;
  margin-bottom: 30px;
  font-size: 32px;
}

.blog-list {
  max-width: 900px;
  margin: auto;
}

.blog-card {
  background: rgb(234, 243, 245);
  padding: 20px;
  border-radius: 14px;
  margin-bottom: 20px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.08);
  transition: transform 0.2s ease;
}

.blog-card:hover {
  transform: translateY(-5px);
}

.blog-card h3 {
  margin-bottom: 10px;
}

.blog-card p {
  margin-bottom: 10px;
  color: #302e2f;
}

.blog-card small {
  color: rgb(28, 27, 27);
}

.delete-btn {
  margin-top: 10px;
  background: #fd7575;
  color: rgb(15, 15, 15);
  border: none;
  padding: 8px 12px;
  border-radius: 8px;
  cursor: pointer;
}

.delete-btn:hover {
  background: #dc2626;
}

.loading {
  text-align: center;
  font-size: 20px;
  margin-top: 50px;
}
